import Books from "./data/Books.json";
import Biblija from "./data/Biblija.json";


export const appVersion = "1.1.0";
export const shareUrl = "https://skrilic.github.io/Biblija-hrv.android/";

export const books = Books.books;
export const bible = Biblija.biblija;

export const getBookEngShorts = () => (
        books.map(item => (item.eng_short))
    )

export const getBook = (bookEngShort) => (
    books.filter(item => (item.eng_short === bookEngShort))[0]
)

export const getVerseId = (book, chapter, verse_no) => (
    bible.filter(verse => (
                verse.book === book && verse.chapter === chapter && verse.verse_no === verse_no
            )
        )[0]._id
)

export const getBookChapterNums = (book) => {
    const noOfChapters = Number(books.filter(bookTitle => (bookTitle.eng_short === book))[0].chapters);
    let chapterArray = [];
    for (let i = 1; i <= noOfChapters; i++) {
            chapterArray.push(i);
        }
    return chapterArray
}

